const dictionary =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789012345678901234567890123456789~`!@#$%^&*()_-=+{}|;:',<>.?/[]~`!@#$%^&*()_-=+{}|;:',<>.?/[]";

const feed = (text, shift, number, specialChars) => {
    const encrypt = (string, type) => {
        let letters = [];

        for (let i = 0; i < string.length; i++) {
            // this check is genuinely not needed
            if (type == "alphabet") {
                if (i == 0) {
                    letters.push(dictionary.indexOf(string[i]));
                } else {
                    letters.push(dictionary.toLowerCase().indexOf(string[i]));
                }
            } else {
                letters.push(dictionary.indexOf(string[i]));
            }
        }

        let lettersEncrypted = [];

        for (let i = 0; i < letters.length; i++) {
            lettersEncrypted.push(letters[i] + shift);
        }

        let cipher = [];

        for (let i = 0; i < lettersEncrypted.length; i++) {
            if (type == "alphabet") {
                if (i % 2 == 0) {
                    cipher.push(dictionary[lettersEncrypted[i]]);
                } else {
                    cipher.push(dictionary[lettersEncrypted[i]].toLowerCase());
                }
            } else {
                cipher.push(dictionary[lettersEncrypted[i]]);
            }
        }

        return cipher;
    };

    const cipherText = encrypt(text, "alphabet");

    let cipherNum;
    let cipherSC;
    let cipher;

    // ditto
    if (number) {
        cipherNum = encrypt(number, "num");
        cipher = cipherText + cipherNum;
    } else if (specialChars) {
        cipherSC = encrypt(specialChars, "sc");
        cipher = cipher + cipherSC;
    } else {
        cipher = cipherText;
    }

    let chunk = cipher.toString();
    chunk = chunk.replace(/,/g, "");

    return chunk;
};

module.exports = feed;
